"use client"

import Image from 'next/image'
import Link from 'next/link'
import { usePathname } from 'next/navigation'
import React from 'react'

const Navbar = () => {

    const pathname = usePathname()

  return (
    <div className="flex justify-between items-center px-20 py-1 bg-amber-100 h-15">
          <div>
            <Image
              src="/job-logo.png"
              alt="Job Logo"
              width={120}
              height={120}
              style={{width: 'auto', height: 'auto'}}
            />
          </div>
          <div className="flex justify-center items-center gap-14"> 
            <Link href={'/'} className={`text-lg font-semibold ${
                pathname === "/" ? "text-white bg-indigo-500 px-6 py-4" : ""
              }`}>Home</Link>
            <Link href={'/form'} className={`text-lg font-semibold ${
                pathname === "/form" ? "text-white bg-indigo-500 px-6 py-4" : ""
              }`}>Apply</Link>
            <Link href={'/list'} className={`text-lg font-semibold ${
                pathname === "/list" ? "text-white bg-indigo-500 px-6 py-4" : ""
              }`}>List</Link>
            {/* <hj5>Home</hj5> */}
          </div>
        </div>
  )
}

export default Navbar
